*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lora', serif;
  font-size: 1.15rem;
  line-height: 1.75rem;
  color: #202020;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d0d9cd;
}

a,
a:hover,
a:visited {
  text-decoration: none;
  color: #0466c8;
}

.header--img {
  background-image: url(/csgc/static/media/sbk014-black.22509117.png);
  background-repeat: no-repeat;
  background-size: 2500px;
  background-position: 20% -260px;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

h1.title {
  font-size: 3.5rem;
  line-height: 1.2;
  font-family: 'Roboto', 'sans-serif';
  text-transform: none !important;
}

h2.title {
  font-size: 2.5rem;
  line-height: 1.2;
  font-family: 'Roboto', 'sans-serif';
  text-transform: none !important;
}

h3.subtitle {
  font-size: 1.75rem;
  padding-top: 0.5rem;
  line-height: 1.2;
  font-family: 'Roboto', 'sans-serif';
  font-weight: normal;
}

h4.subhead {
  font-family: 'Roboto', 'sans-serif';
  font-size: 1.75rem;
  font-weight: bold;
  text-transform: uppercase;
}

h5.subsubhead {
  font-family: 'Roboto', 'sans-serif';
  font-size: 1.4rem;
  padding: 1rem 0;
}

h5.subsubnormal {
  font-family: 'Roboto', 'sans-serif';
  font-size: 1.4rem;
  padding: 1rem 0;
  font-weight: normal;
}

section {
  width: 50vw;
  margin: 0 auto;
  padding: 2rem 0;
}

/* .wide {
  padding: 0 100px;
} */

nav {
  padding-top: 300px;
}

.nav--text {
  font-family: 'Montserrat', 'sans-serif';
  font-weight: bold;
  font-size: 1em;
  text-transform: uppercase;
}

.nav--text ul {
  list-style-type: none; /* removes bullets */
  margin: 0;
  padding: 0;
}

.nav--text li {
  display: inline;
  padding: 0.8rem;
}

.nav--text a {
  color: #557c55;
}

.nav--text a:hover {
  color: #24a19c;
}

p:first-of-type {
  padding-top: 1.5rem;
}

p:not(:last-child) {
  padding-bottom: 1.5rem;
}

.title--ddd {
  font-family: 'Roboto Condensed', 'sans-serif';
  font-size: 1rem;
  font-weight: normal;
  text-transform: uppercase;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
} */

.marker-btn {
  background: none;
  border: none;
  cursor: pointer;
}

.marker-btn img {
  width: 20px;
  height: 20px;
}

ul {
  padding-left: 2em;
}

li:not(:last-child) {
  padding-bottom: 0.3em;
}

.highlight-box {
  background-color: rgba(217, 194, 182, 0.5);
  padding: 1.5rem;
  margin-top: 1.5rem;
  /* border-radius: 2px 40px; */
  font-family: 'Roboto', 'sans-serif';
  font-size: 1rem;
  line-height: 1.5;
  width: 100%;
  margin: 0 auto;
}

.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 4px;
}

.arrow-down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

blockquote {
  font-style: italic;
  margin-left: 2.5em;
  quotes: '“' '”' '“' '”';
}

blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.15em;
  margin-left: -0.5em;
  vertical-align: -0.4em;
}

blockquote::after {
  color: #ccc;
  content: close-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-left: 0.1em;
  vertical-align: -0.45em;
}

