}
*/

/* stacked bars */
.x-axis,
.y-axis {
  color: #2e2624;
  font-size: 0.8em;
}

.x-axis > path.domain {
  color: #2e2624;
  stroke-width: 1;
}

.x-axis .tick text {
  font-size: 0.95em;
}

/* axis title */
.x-axis > text,
.y-axis > text {
  fill: #686868;
  font-size: 0.9em;
}

.y-axis > path.domain {
  opacity: 0;
}

.y-axis .tick line {
  opacity: 0;
}

.x-axis .tick line {
  opacity: 0;
}

.y-gridlines {
  color: #c8c8c8;
  stroke-width: 0.75;
}

.chart--title {
  font-size: 16px;
  font-weight: bold;
  font-family: 'Roboto', 'sans-serif';
}

.vis-caption {
  font-size: 0.85rem;
  font-family: 'Roboto', 'sans-serif';
  line-height: 1.2em;
  /* padding-top: 25%; */
}

.vis-caption-small {
  font-size: 13px;
  font-family: 'Roboto Condensed', 'sans-serif';
  line-height: 1.2em;
  color: #686868;
}

hr.dashed {
  border: 0;
  border-bottom: 1px dashed #ccc;
  background: #999;
  margin-bottom: 2rem;
}

/* data table */
#tableContainer {
  background-color: rgba(240, 236, 227, 0.35);
  font-family: 'Roboto', 'sans-serif';
  font-size: 0.8em;
  line-height: 1.2;
  width: 80vw;
  border: 1px solid black;
  padding: 0.5em;
  border-collapse: collapse;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);
}

#tableContainer th {
  text-align: right;
  border-bottom: 1px solid #97bfb4;
  padding-bottom: 0.5em;
  vertical-align: bottom;
}

#tableContainer th:first-child {
  text-align: left;
}

#tableContainer th:not(:last-child) {
  padding-right: 1em;
}

#tableContainer td:first-child {
  text-align: left;
}

#tableContainer td:not(:last-child) {
  padding-right: 1em;
}

#tableContainer td {
  text-align: right;
  padding-top: 0.5em;
}

/* scrollytelling */
.container {
  display: flex;
}

.steps {
  width: 35%;
}

.step {
  margin-top: 500px;
  min-height: 100px;
}

#chart {
  margin-right: 5px;
  position: -webkit-sticky;
  position: sticky;
  top: 25px;
  left: 0px;
  width: 800px;
  height: 750px;
}

/* shiny dashboards */
.flex-container-shiny {
  display: flex;
  border: 1px dotted #529c72;
  padding: 1.25em;
}

.flex-container-bubble {
  display: flex;
  padding: 0.25em;
}

.flex-child {
  flex: 1 1;
}

.flex-child:first-child {
  margin-right: 50px;
}

.button {
  border: none;
  background-color: #146356;
  padding: 10px 25px;
  display: inline-block;
  margin-top: 1rem;
}

a.button--link {
  font-family: 'Roboto Condensed', 'sans-serif';
  font-size: 1.5em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: regular;
  color: white !important;
}

/* bubbles */
.text--bubbles {
  text-anchor: middle;
  font-size: 12px;
  fill: #ffffff;
  font-family: 'Roboto Condensed', 'sans-serif';
  pointer-events: none;
  opacity: 0;
}

.tooltip-text {
  display: inline-block;
  padding: 5px 5px;
  border-radius: 2px;
  font-size: 14px;
  line-height: 1.2;
  font-family: 'Roboto Condensed', 'sans-serif';
}

.tooltip {
  position: absolute;
  visibility: hidden;
  background-color: #2e2624;
  color: #ffffff;
  display: flex;
  max-width: 200px;
  opacity: 0.95;
}

.tooltip div {
  padding: 10px;
}

.scroll-button {
  position: fixed;
  width: 100%;
  left: 45%;
  bottom: 30px;
  height: 40px;
  z-index: 1;
  cursor: pointer;
  color: #24a19c;
}

/* footer */
footer {
  margin-top: auto;
  background-color: #293f32;
}

.footer--bar {
  display: flex;
  justify-content: space-between;
  bottom: 0;
  align-items: center;
  height: 4rem;
}

.footer--image {
  vertical-align: middle;
  max-width: 100%;
  height: 2vw;
}

.footer--text {
  font-family: 'Montserrat', 'sans-serif';
  text-transform: uppercase;
  font-size: 1.1rem;
  font-weight: normal;
}

.footer--text a {
  color: #96ceb4;
  text-decoration: none;
}

.footer--text a:hover {
  color: #24a19c;
}

.footer--text img {
  width: 15px;
}

.footer--text ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer--text li {
  display: inline;
  padding: 0.8rem;
}

#map {
  top: 0;
  height: 100vh;
  position: sticky;
  position: -webkit-sticky;
}

#features {
  padding-top: 10vh;
  padding-bottom: 10vh;
}

.hidden {
  visibility: hidden;
}

.centered {
  width: 50vw;
  margin: 0 auto;
}

.lefty {
  width: 33vw;
  margin-left: 5vw;
}

.righty {
  width: 33vw;
  margin-left: 62vw;
}

.fully {
  width: 100%;
  margin: auto;
}

.light {
  color: #444;
  background-color: #fafafa;
}

.dark {
  color: #fafafa;
  background-color: #444;
}

.step {
  padding-bottom: 50vh;
  /* margin-bottom: 10vh; */
  opacity: 0.25;
}

.step.active {
  opacity: 0.8;
}

.step div {
  padding: 25px 50px;
  line-height: 25px;
  font-size: 1rem;
  font-family: 'Roboto', 'sans-serif';
  box-shadow: 0 3px 8px rgb(0 0 0 / 0.2);
}

.step img {
  width: 100%;
}

@media (max-width: 750px) {
  .centered,
  .lefty,
  .righty,
  .fully {
    width: 90vw;
    margin: 0 auto;
  }
}

/* Fix issue on mobile browser where scroll breaks  */
.mapboxgl-canvas-container.mapboxgl-touch-zoom-rotate.mapboxgl-touch-drag-pan,
.mapboxgl-canvas-container.mapboxgl-touch-zoom-rotate.mapboxgl-touch-drag-pan .mapboxgl-canvas {
  touch-action: unset;
}

